
.pagination {
    display: flex;
    justify-content: center;
}

.page-link {
    color: #007bff;
    cursor: pointer;
    text-decoration: none;
    padding: 5px 10px;
    border: 1px solid #007bff;
    margin: 0 5px;
}

.page-link:hover {
    background-color: #007bff;
    color: white;
}
